<template>
  <footer class="footer fixed">
    <notifications class="alert-info"   group="foo" />
    <div class="container-fluid d-flex flex-wrap justify-content-between">
      <nav>
        <ul>
          <li>
             <button :disabled="store.state.keychain.rowKeyChain < 20" :class="{'activeButton': store.state.keychain.selectedItemByPage === 20 }" @click="setPaginationTable(20)" class="item-table-selector pointer">20</button>
             <button :disabled="store.state.keychain.rowKeyChain < 50" :class="{'activeButton': store.state.keychain.selectedItemByPage === 50 }" @click="setPaginationTable(50)" class="item-table-selector pointer">50</button>
             <button :disabled="store.state.keychain.rowKeyChain < 100" :class="{'activeButton': store.state.keychain.selectedItemByPage === 100 }" @click="setPaginationTable(100)" class="item-table-selector pointer">100</button> keys per page
          </li>
          <li class="fixed-bottom-right">
            {{store.state.keychain.startPaginationItem}} - {{store.state.keychain.endPaginationItem}} of {{store.state.keychain.rowKeyChain}}
             <button :disabled="store.state.keychain.startPaginationItem === 1" @click="move('backward')" class="item-table-selector arrow pointer"><i class="ti-angle-left grey"></i></button>
             <button @click="move('forward')" class="item-table-selector arrow pointer" ><i class="ti-angle-right grey"></i></button>
          </li>
        </ul>
      </nav>
      <div class="copyright d-flex flex-wrap">
       
      </div>
    </div>
  </footer>
</template>
<script>
import store from "@/flulux/store";
export default {
  data() {
    return {
      store: store
    };
  },
  methods: {
    /**
     * map pagination to data store
     */
    setPaginationTable(p) {
      if (store.state.keychain.rowKeyChain < p) {
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          store.state.keychain.rowKeyChain
        );
        this.$notify({
          group: "foo",
          title: "Keys par page is adapted to your number of keys",
          classes: "vue-notification error"
        });
      } else {
        this.store.actions.SET_PAGINATION_KEYCHAIN_TABLE(p);
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(p);
      }
    },
    move(state) {
      if (state === "forward") {
        if (
          store.state.keychain.selectedItemByPage +
            store.state.keychain.startPaginationItem >
            store.state.keychain.rowKeyChain ||
          store.state.keychain.selectedItemByPage +
            store.state.keychain.endPaginationItem +
            1 >
            store.state.keychain.rowKeyChain
        )
          return this.setPaginationTable(store.state.keychain.rowKeyChain + 1);
        this.store.actions.SET_START_PAGINATION_KEYCHAIN(
          store.state.keychain.selectedItemByPage +
            store.state.keychain.startPaginationItem
        );
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          store.state.keychain.selectedItemByPage +
            store.state.keychain.endPaginationItem +
            1
        );
      } else {
        if (
          store.state.keychain.selectedItemByPage -
            store.state.keychain.startPaginationItem >
            store.state.keychain.startPaginationItem ||
          store.state.keychain.selectedItemByPage -
            store.state.keychain.endPaginationItem >
            store.state.keychain.endPaginationItem
        )
          return this.setPaginationTable(store.state.keychain.rowKeyChain + 1);
        this.store.actions.SET_START_PAGINATION_KEYCHAIN(
          store.state.keychain.startPaginationItem -
            store.state.keychain.selectedItemByPage
        );
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(
          store.state.keychain.endPaginationItem -
            1 -
            store.state.keychain.selectedItemByPage
        );
      }
    }
  }
};
</script>
<style>
.fixed {
  z-index: 999999999 !important;
  position: fixed !important;
  bottom: 0;
  width: 83%;
  height: 60px;
  border: solid 1px #e6e6e6;
  background: #fdfdfd;
}

.item-table-selector {
  background-color: white;
  border: none;
  width: 60px;
  height: 30px;
}

.activeButton {
  background: #a6a6a6;
  border-radius: 12%;
  color: white;
}
.arrow {
  border: 1px solid #e6e6e6;
  border-radius: 10%;
}
.grey {
  color: #a6a6a6;
}
.pointer {
  cursor: pointer;
}
.fixed-bottom-right {
  position: fixed;
  right: 0;
}
</style>
