<template>
    <div class="limiter">
		<notifications class="alert-info"   group="foo" />
		<div class="container-login100">
			<div class="wrap-login100">
				    
					<div class="login100-more">
						<img class="cover-img" src="@/assets/img/havrassembly@3x.png">
					</div>
			<div class="form-container">
					<div class="login100-form validate-form">
							<span class="login100-form-title p-b-43">
								<img  src="@/assets/img/logo-final-havr-01.jpg">
							</span>
							
							
							<div class="wrap-input100 validate-input">
								<input placeholder="" v-model="state.email_or_username" class="input100" type="text" name="text">
								<span class="focus-input100"></span>
								 <transition name="fade">
								<span  v-if="state.email_or_username < 1" class="label-input100">Email or username</span>
								 </transition>
							</div>

							
							
							
							<div class="wrap-input100 validate-input" data-validate="Password is required">
								<input  placeholder="" v-model="state.password" class="input100" type="password" name="pass">
								<span class="focus-input100"></span>
								<transition name="fade">
								<span v-if="state.password.length < 1" class="label-input100">Password</span>
								</transition>
							</div>
		
							<div class="flex-sb-m w-full p-t-3 p-b-32 forgot-password">
								<div class="contact100-form-checkbox">
								
								</div>
		
								<div>
									<a href="#" class="txt1">
										Forgot Password?
									</a>
								</div>

							
							</div>
					
		
							<div class="container-login100-form-btn">
								<button @click="login" class="login100-form-btn">
									Sign in
								</button>
							</div>
								<p class="new-account">First time here ? Create an account</p>
						</div>
			</div>
				

				
			</div>
		</div>
	</div>
	
</template>

<script>
import loginRepository from "@/repository/login";
import { dataRepository } from "@/repository/login";
export default {
  data() {
    return {
      state: {
        grant_type: "password",
        email_or_username: "",
        password: "",
        client_id: "web_plateform_client_id",
        user_agent: navigator.userAgent
      }
    };
  },
  mounted() {
		 this.checkIfUserIsConnected() 
  },
  methods: {
    /**
     * log user into application using data repository
     */
    login() {
      loginRepository
        .login(this.state)
        .then(res => this.checkIfUserIsConnected());
    },
    /**  * check acccess token props to log user
     */
    checkIfUserIsConnected() {
      if (!dataRepository.account.access_token) return this.showErrMsg();
      else this.$router.push("/keychain");
		},
    /**     * bind error msg if credentials are wrong
     */
    showErrMsg() {
      this.$notify({
        group: "foo",
        title: "Incorrect credentials",
        classes: "vue-notification error"
      });
    }
  }
};
</script>



<style lang="scss" scoped>
@import "../assets/sass/login.scss";
</style>
