import {Dispatcher} from './dispatcher'
export const SET_PAGINATION_KEYCHAIN_TABLE = async (p) => await Dispatcher.$emit('SET_PAGINATION_KEYCHAIN', p)
export const SET_NB_ROW_KEYCHAIN_TABLE = async (p) => await Dispatcher.$emit('SET_NB_ROW_KEYCHAIN', p)
export const OPEN_DETAILS_DRAWER = async (p) => await Dispatcher.$emit('OPEN_DRAWER', p)
export const SET_DETAILS_KEYCHAIN = async (p) => await Dispatcher.$emit('SET_DETAILS_KEY', p)
export const SET_START_PAGINATION_KEYCHAIN = async (p) => await Dispatcher.$emit('SET_START_KEYCHAIN', p)
export const SET_END_PAGINATION_KEYCHAIN = async (p) => await Dispatcher.$emit('SET_END_KEYCHAIN', p)