<template>
  <div class="sidebar"
       :data-background-color="backgroundColor"
       :data-active-color="activeColor">
    <div class="sidebar-wrapper" id="style-3">
      <div class="logo">
        <a href="#" class="simple-text">
            <div class="logo-img">
                <img v-if="user.profile_picture" :src="user.profile_picture" alt="">
                <img v-else src="@/assets/img/usermock.jpeg" alt="">
            </div>
             
          <span class="first-name-container">{{displayName}} <i class="ti-angle-down arrowLog left-margin"></i></span>
          <div class="username-container"><span>{{user.username}}</span></div>
        </a>
      </div>
      <slot>

      </slot>
      
      <ul class="nav">
         <moving-arrow :move-y="arrowMovePx">

      </moving-arrow>
        <slot name="links">
          
          <sidebar-link v-for="(link,index) in sidebarLinks"
                        :key="index"
                        :to="link.path"
                        :name="link.name"
                        :icon="link.icon">
          </sidebar-link>
        </slot>
      </ul>
     <div class="container-footer">
        <button class="newKey">NEW CONTACT</button>
       <button class="shareKey">SHARE KEY</button>
     </div>
    </div>
  </div>
</template>
<script>
import MovingArrow from "./MovingArrow.vue";
import SidebarLink from "./SidebarLink";
import {dataRepository} from '@/repository/login'
export default {
  props: {
    backgroundColor: {
      type: String,
      default: "black",
      validator: value => {
        let acceptedValues = ["white", "black", "darkblue"];
        return acceptedValues.indexOf(value) !== -1;
      }
    },
    activeColor: {
      type: String,
      default: "success",
      validator: value => {
        let acceptedValues = [
          "primary",
          "info",
          "success",
          "warning",
          "danger"
        ];
        return acceptedValues.indexOf(value) !== -1;
      }
    },
    sidebarLinks: {
      type: Array,
      default: () => []
    },
    autoClose: {
      type: Boolean,
      default: true
    }
  },
  provide() {
    return {
      autoClose: this.autoClose,
      addLink: this.addLink,
      removeLink: this.removeLink
    };
  },
  components: {
    MovingArrow,
    SidebarLink
  },
  computed: {
    /**
     * Styles to animate the arrow near the current active sidebar link
     * @returns {{transform: string}}
     */
    arrowMovePx() {
      return this.linkHeight * this.activeLinkIndex;
    }
  },
  data() {
    return {
      user: {...dataRepository.account.user},
      displayName: '',
      linkHeight: 65,
      activeLinkIndex: 0,
      windowWidth: 0,
      isWindows: false,
      hasAutoHeight: false,
      links: []
    };
  },
  methods: {
    findActiveLink() {
      this.links.forEach((link, index) => {
        if (link.isActive()) {
          this.activeLinkIndex = index;
        }
      });
    },
    addLink(link) {
      const index = this.$slots.links.indexOf(link.$vnode);
      this.links.splice(index, 0, link);
    },
    removeLink(link) {
      const index = this.links.indexOf(link);
      if (index > -1) {
        this.links.splice(index, 1);
      }
    }
  },
  mounted() {
    this.displayName = this.user.first_name + ' ' + this.user.last_name
    this.$watch("$route", this.findActiveLink, {
      immediate: true
    });
  }
};
</script>
<style>
.container-footer {
  position: fixed;
  bottom: 0;
  width: 260px;
}
.shareKey{
  border-radius: 6px;
  background-color: #ff5c00;
  border: solid 1px #b3b3b3;
  color: white;
  border: none;
  width: 216px;
  height: 44px;
     margin-bottom: 16px;
    margin-left: 16px;
    margin-right: 16px;
}
.newKey{
  border: solid 1px #b3b3b3!important;
  border-radius: 6px;
  background-color: white;
  color: #b3b3b3;
  
  border: none;
  width: 216px;
  height: 44px;
     margin-bottom: 16px;
    margin-left: 16px;
    margin-right: 16px;
}

.arrowLog {
      font-size: xx-small;
    color: #a6a6a6;
}
.first-name-container{
position: absolute;
    top: 20px;

}
.left-margin  {
      margin-left: 5px;
}
.username-container {
      position: absolute;
    left: 80px;
    top: 40px;
    font-size: smaller;
    color: #a6a6a6;
}
</style>
