
import { ENDPOINTS, API_BASE_URL } from '../constant.js'
import { dataRepository } from '@/repository/login'
import loginRepository from './login'
export default {
    /**
    * login wrapper to get access token
    * @param {access_token} p 
    */
    async getDoorLocks(p) {
     await loginRepository.retrieveToken().then(res => {
          this.retrieveLockDoor(res)
     })
    },

    /**
     * get all doorlock of a user
     * @param {user token} token 
     */
    async retrieveLockDoor(token) {
        await fetch(API_BASE_URL + ENDPOINTS.keychain, {
            method: "GET",
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + token}
        }).then(resp => {
            if (resp.status === 200) {
                resp.json().then(res => {
                    keychainRepository.door_locks = [...res]
                })
            }
            else {
                location.href = '/#/login'
            }
        }
        )
    },


    async getLogs(p) {
        await loginRepository.retrieveToken().then(res => {
          this.retrieveLockDoorLogs(p, res)
        })
    },

    /**
     * get log of key
     */
    async retrieveLockDoorLogs(p, token) {
       await fetch(API_BASE_URL + ENDPOINTS.doorlocklog, {
            method: 'post',
            headers: {"Content-Type": "application/json", 'Authorization': 'Bearer: ' + token},
            body: JSON.stringify({notification: {door_lock_id: p}})
          }).then(res => {
              res.json()
          }).then(log => {
              console.log(log)
              return log
          })
    },
}

export let keychainRepository = {
    door_locks: []
}