<template>
  <table class="table margin" :class="tableClass">
    <thead>
    <slot name="columns">
      <th class="capitalize" v-for="column in columns" :key="column">{{column}} <img class="filter-th" src="@/assets/havr-icon/filterTableIcon.svg"></th>
    </slot>
    </thead>
    <tbody>
    <tr v-for="(item, index) in data.slice(store.state.keychain.startPaginationItem - 1, store.state.keychain.endPaginationItem)" :key="index">
      <slot :row="item">
        <td @click='setDetailsItem(item)' class="margin-td capitalize" v-for="(column, index) in columns"
            :key="index"
      >
          <div  class="container-table"><div class="valid" v-if="column === 'status'"><i class="ti-check light"></i></div> {{item[column]}}</div>
        </td>
      </slot>
    </tr>
    </tbody>
  </table>
  
</template>
<script>
import store from "@/flulux/store";
import lodash from "lodash";
import { keychainRepository } from "@/repository/keychain";
export default {
  name: "paper-table",
  data() {
    return {
      store: store
    };
  },
  props: {
    columns: Array,
    data: Array,
    type: {
      type: String, // striped | hover
      default: "striped"
    },
    maxRow: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    }
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    setDetailsItem(p) {
      const obj = lodash.filter(keychainRepository.door_locks, { id: p.id });
      this.store.actions.OPEN_DETAILS_DRAWER(true)
      this.store.actions
        .SET_DETAILS_KEYCHAIN(obj[0])
        .then(res => console.log(this.store.state.keychain));
    }
  }
};
</script>
<style>
.margin-td {
  padding-bottom: 20px !important;
  padding-top: 20px !important;
}

.table > tbody > tr {
  position: relative;
  border-bottom: 2px solid #e6e6e6;
}

.capitalize {
  text-transform: capitalize;
}
.grey {
  color: #e6e6e6;
}
.valid {
  width: 14px;
  height: 14px;
  margin-right: 10px;
  border-radius: 10px;
  background-color: #c6f363;
  position: relative;
  top: 2px;
}
.container-table {
  display: flex;
}
.light {
  color: white;
}
.margin {
  position: relative;
  top: 8em;
}
.filter-th {
  margin-left: 25px;
}
</style>
